import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import Usuario from '../../servi√ßos/getUsuario'

@Component({
  selector: 'app-modal-info',
  templateUrl: './modal-info.component.html',
  styleUrls: ['./modal-info.component.css']
})
export class ModalInfoComponent implements OnInit {

  usuario = new Usuario();

  constructor() { }

  ngOnInit() {
    setTimeout(() => {
      $('.event span').click((e)=>{
        this.usuario.consultarMovimentacao();

        var infoId = parseInt(e.target.id);
        
        $('#modalInfo').click();

        setTimeout(() => {
          if(this.usuario.movimentacao[infoId].tipoMovimentacao != 'Recebimento'){
            $("#conta").text('Conta destino');
            $("#contaModalInfo").val(this.usuario.movimentacao[infoId].contaDestino);
            $("#dtModalInfo").val(this.usuario.movimentacao[infoId].dtMovimentacao);
            $("#valorModalInfo").val(`R$ ${this.usuario.movimentacao[infoId].valor}`);
            $("#tipoModalInfo").val(this.usuario.movimentacao[infoId].tipoMovimentacao);
          }else{
            $("#conta").text('Conta origem');
            $("#contaModalInfo").val(this.usuario.movimentacao[infoId].contaOrigem);
            $("#dtModalInfo").val(this.usuario.movimentacao[infoId].dtMovimentacao);
            $("#valorModalInfo").val(`R$ ${this.usuario.movimentacao[infoId].valor}`);
            $("#tipoModalInfo").val(this.usuario.movimentacao[infoId].tipoMovimentacao);
          }
          
        }, 200);
      })
 
    }, 200);
  }

}
