import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import { ToastrService } from 'ngx-toastr'; 
import Usuario from '../../servi√ßos/getUsuario';

@Component({
  selector: 'app-debito',
  templateUrl: './debito.component.html',
  styleUrls: ['./debito.component.css']
})
export class DebitoComponent implements OnInit {

  usuario = new Usuario();

  mascara = "0000000000000000000";

  verificarCartao(){
    var numCartao = (<HTMLInputElement>document.getElementById("numDebito")).value.length
    if(numCartao <= 16){
      this.mascara = "0000 0000 0000 0000"
    }else{
      this.mascara = "000000 0000 00000 0000"
    }
  }

  constructor(private toastr: ToastrService) { }

  ngOnInit() {
    document.getElementById('depositarDebito').addEventListener('click',()=>{
      this.usuario.cadastrarDeposito();
      this.toastr.success("Concluido");
      setTimeout(() => {
        this.toastr.clear();
      }, 1500);
      location.reload();
    })
  }

}
