import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';   
import { Router } from '@angular/router';
import * as $ from 'jquery'
import Usuario from '../servi√ßos/getUsuario'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  tipoSenha = false;

  constructor(private toastr: ToastrService,private router: Router) { }

  getUsuario(){
    var usuario = new Usuario();
    usuario.pegarDadosUsuario();

    setTimeout(() => {
      if(usuario.email != undefined && usuario.senha == (<HTMLInputElement>document.getElementById('login_senha')).value){
        this.toastr.clear();
        this.toastr.success('Sucesso').onShown.subscribe(()=>{
          setTimeout(() => {
            this.toastr.clear();
          }, 1500);
        })
        this.router.navigate(['/home']);
      }else{
        this.toastr.clear()
        this.toastr.warning('Email ou senha incorretos.').onTap.subscribe(()=>{
          $("#modalCadastroButton").click();
        })
      }
    }, 200);

  }

  ngOnInit() {

    this.toastr.info("Clique para fazer cadastro","Novo por aqui?").onTap.subscribe(()=>{
      $("#modalCadastroButton").click();
    })

    setInterval(()=>{
      if(window.innerWidth <= 500){
        $(".divLogo").addClass("col-0");
        $("#form").addClass("col-12");
        document.getElementById("divLogo").style.display = 'none';
      }else{
        $(".divLogo").addClass("col-6");
        $("#form").addClass("col-5");
        document.getElementById("divLogo").style.display = 'block';
      }
    })   

    document.getElementById('eyePassword').addEventListener('click',()=>{
      (<HTMLInputElement>document.getElementById('login_senha')).type = this.tipoSenha ? 'password': 'text'; 
      this.tipoSenha = !this.tipoSenha;
    })
    
  }

}
