import * as $ from 'jquery';

export default class Usuario {

    constructor() { }

    //Usuario
    nome;
    conta;
    email;
    cpf;
    dt;
    senha;
    token = document.cookie.split('=')[1];
    saldo;

    //Movimenta√ß√£o
    movimentacao = [{
        idMovimentacao: '',
        idCliente: '',
        dtMovimentacao: '',
        tipoMovimentacao: '',
        valor: '',
        contaOrigem: '',
        contaDestino: '',
    }]

    pegarDadosUsuario(){
        $.ajax({
            type: 'POST',
            url: 'http://127.0.0.2:8080/api/consulta/cliente',
            data: {
                Email: (<HTMLInputElement>document.getElementById('login_email')).value,
                Senha: (<HTMLInputElement>document.getElementById('login_senha')).value,
            },
            success: (data)=>{
                this.nome = data.results[0].NOME_CLIENTE;
                this.email = data.results[0].EMAIL_CLIENTE;
                this.cpf = data.results[0].CPF_CLIENTE;
                this.dt = data.results[0].DT_CLIENTE;
                this.senha = data.results[0].SENHA_CLIENTE;
                this.token = data.results[0].TOKEN_CLIENTE;

                document.cookie = `tk=${data.results[0].TOKEN_CLIENTE}; expires=Thu, 01 Jan 2029 12:00:00 UTC`;
                return;
            }
        });
    }

    validarTokenUsuario(){
        $.ajax({
            type: 'POST',
            url: 'http://127.0.0.2:8080/api/validar/cliente',
            data: {
              TOKEN: this.token,
            },
            success: (data)=>{
                this.conta  = data.results[0].CONTA_CLIENTE;
                this.nome = data.results[0].NOME_CLIENTE;
                this.email = data.results[0].EMAIL_CLIENTE;
                this.cpf = data.results[0].CPF_CLIENTE;
                this.dt = data.results[0].DT_CLIENTE.replace('T02:00:00.000Z','').replace('T03:00:00.000Z','');
                this.senha = data.results[0].SENHA_CLIENTE;
                this.token = data.results[0].TOKEN_CLIENTE;
                this.saldo = data.results[0].SALDO_CONTA;
            }
        });
    }

    consultarMovimentacao(){
        $.ajax({
            type: 'POST',
            url: 'http://127.0.0.2:8080/api/consulta/movimentacao',
            data: {
              TOKEN: this.token,
            },
            success: (data)=>{
                for(var i = 0; i < 200; i++){
                    this.movimentacao.push({
                        idMovimentacao: data.results[i].ID_MOVIENTACAO,
                        idCliente: data.results[i].ID_CLIENTE,
                        dtMovimentacao: data.results[i].DT_MOVIMENTACAO.replace('T03:00:00.000Z',''),
                        tipoMovimentacao: data.results[i].TIPO_MOVIMENTACAO,
                        valor: data.results[i].VALOR,
                        contaOrigem: data.results[i].CONTA_ORIGEM,
                        contaDestino: data.results[i].CONTA_DESTINO,
                    })
                }
            }
        });
    }

    cadastrarDeposito(){
        var valorDeposito;

        if($('#payForm').val() == 'boleto'){
            valorDeposito = (<HTMLInputElement>document.getElementById('valorBoleto')).value;
        }
        if($('#payForm').val() == 'credito'){
            valorDeposito = (<HTMLInputElement>document.getElementById('valorCredito')).value;
        }
        if($('#payForm').val() == 'deposito'){
            valorDeposito = (<HTMLInputElement>document.getElementById('valorDebito')).value;
        }

        $.ajax({
            type: 'POST',
            url: 'http://127.0.0.2:8080/api/cadastro/movimentacao',
            data: {
              TOKEN: document.cookie.split('=')[1],
              DT_MOVIMENTACAO: `${new Date().getFullYear().toString()}-${new Date().getMonth().toString()}-${new Date().getDate().toString()}`,
              TIPO_MOVIMENTACAO: "Dep√≥sito",
              VALOR: parseInt(valorDeposito.replaceAll('.','')),
            }
        });
    }

    cadastrarTransferencia(){
        this.validarTokenUsuario();

        var select = (<HTMLSelectElement>document.getElementById('transferenciaForm'));
        var valorTransferencia;
        if(select.options[select.selectedIndex].value == 'banco'){
            valorTransferencia = (<HTMLInputElement>document.getElementById('valorBanco')).value;
        }
        if(select.options[select.selectedIndex].value == 'carteira'){
            valorTransferencia = (<HTMLInputElement>document.getElementById('valorCarteira')).value;
        }

        setTimeout(() => {
            $.ajax({
                type: 'POST',
                url: 'http://127.0.0.2:8080/api/cadastro/movimentacao',
                data: {
                  TOKEN: document.cookie.split('=')[1],
                  DT_MOVIMENTACAO: `${new Date().getFullYear().toString()}-${new Date().getMonth().toString()}-${new Date().getDate().toString()}`,
                  TIPO_MOVIMENTACAO: 'Transfer√™ncia',
                  VALOR: valorTransferencia.replace('.','').replace('.','').replace('.','').replace('.',''),
                  CONTA_ORIGEM: parseInt(this.conta),
                  CONTA_DESTINO: parseInt((<HTMLInputElement>document.getElementById('numeroContaCarteira')).value)
                }
            })
            setTimeout(() => {
                location.reload();
            }, 300);
        }, 300);
    }

    alterarUsuario(){
        $.ajax({
            type: 'POST',
            url: 'http://127.0.0.2:8080/api/alterar/cliente',
            data: {
                Nome: $("#nomeModalPerfil").val(),
                Email: $("#emailModalPerfil").val(),
                CPF: $("#cpfModalPerfil").val(),
                DT: $("#dtModalPerfil").val(),
                TOKEN: document.cookie.split('=')[1],
            }
        })
        location.reload();
    }

}
