import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NegociarHomeComponent } from './negociar-home/negociar-home.component';
import { CriptomoedaHomeComponent } from './criptomoeda-home/criptomoeda-home.component';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'home', component: NegociarHomeComponent},
  {path: 'criptomoedas', component: CriptomoedaHomeComponent },
  {path: '**', redirectTo: '', pathMatch: 'full' },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
