import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import { ToastrService } from 'ngx-toastr';   
import Usuario from '../../servi√ßos/getUsuario';

@Component({
  selector: 'app-banco',
  templateUrl: './banco.component.html',
  styleUrls: ['./banco.component.css']
})
export class BancoComponent implements OnInit {

  usuario = new Usuario();

  cadastrarTransferencia(){
    this.usuario.validarTokenUsuario();
    setTimeout(() => {
      
      var saldo = parseInt($('#saldo').text().replace(/ /g,'').replace('R$',''));
      var saldoTransferencia = parseInt((<HTMLInputElement>document.getElementById('valorCarteira')).value.replace('.','').replace('.','').replace('.',''));
      var contaUsuario = parseInt(this.usuario.conta);
      var contaDigitada = parseInt((<HTMLInputElement>document.getElementById('numeroContaCarteira')).value);

      if(saldo <= saldoTransferencia || contaUsuario == contaDigitada){
        this.toastr.error("N√£o foi possivel realizar a transa√ß√£o",'Revise os dados');
        setTimeout(() => {
          this.toastr.clear();
        }, 1500);
      }else{
        this.usuario.cadastrarTransferencia();
        this.toastr.success("Concluido");
        setTimeout(() => {
          this.toastr.clear();
        }, 1500);
      }

    }, 300);

  }

  constructor(private toastr: ToastrService) { }

  ngOnInit() {
    Usuario.prototype.validarTokenUsuario();
  }

}
