import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import Usuario from '../servi√ßos/getUsuario'

@Component({
  selector: 'app-negociar-home',
  templateUrl: './negociar-home.component.html',
  styleUrls: ['./negociar-home.component.css']
})
export class NegociarHomeComponent implements OnInit {

  usuario = new Usuario();

  saldo;
  saldoBool = false;

  //Usuario
  nome;
  email;
  cpf;
  dt;
  senha;

  sairConta(){
    document.cookie = `tk=; expires=`;
  }

  constructor() { }

  ngOnInit() {
    if(document.cookie.split('=')[1] != ''){
      this.usuario.validarTokenUsuario();

      setTimeout(() => {
        this.nome = `${this.usuario.nome.split(' ')[0]} ${this.usuario.nome.split(' ')[1]}`;
        this.email = this.usuario.email;
        this.cpf = this.usuario.cpf;
        this.dt = this.usuario.dt;
        this.senha = this.usuario.senha;
        this.saldo = this.usuario.saldo;
      }, 200);
  
      setInterval(()=>{
        if(this.saldoBool == false){
          $("#saldo").addClass('saldoHide');
          $("#eye").removeClass('fa fa-eye-slash');
          $("#eye").addClass('fa fa-eye')
        }else{
          $("#saldo").removeClass('saldoHide');
          $("#eye").removeClass('fa fa-eye');
          $("#eye").addClass('fa fa-eye-slash');
        }
      })
    }else{
      location.href = '/'
    }
    
  }

}
