import { Component, OnInit } from '@angular/core';
import Usuario from '../servi√ßos/getUsuario'
import * as $ from 'jquery'

@Component({
  selector: 'app-negociar-home-movimentacao',
  templateUrl: './negociar-home-movimentacao.component.html',
  styleUrls: ['./negociar-home-movimentacao.component.css']
})
export class NegociarHomeMovimentacaoComponent implements OnInit {

  usuario = new Usuario;

  movimentacao = [{}];

  constructor() { }

  ngOnInit() {

    this.usuario.consultarMovimentacao();

    setTimeout(() => {
      for(var i = 1; i < 10; i++){

        if(this.usuario.movimentacao[1].idMovimentacao != ''){
          this.movimentacao.push({
            idMovimentacao: this.usuario.movimentacao[i].idMovimentacao,
            idCliente: this.usuario.movimentacao[i].idCliente,
            dtMovimentacao: `${this.usuario.movimentacao[i].dtMovimentacao.split('-')[2]}/${this.usuario.movimentacao[i].dtMovimentacao.split('-')[1]}/${this.usuario.movimentacao[i].dtMovimentacao.split('-')[0]}`,
            tipoMovimentacao: this.usuario.movimentacao[i].tipoMovimentacao,
            valor: this.usuario.movimentacao[i].valor,
            contaOrigem: this.usuario.movimentacao[i].contaOrigem,
            contaDestino: this.usuario.movimentacao[i].contaDestino
          })

        }else if(this.usuario.movimentacao[0].idMovimentacao != ''){
          this.movimentacao.push({
            dtMovimentacao: undefined,
            tipoMovimentacao: undefined
          })
          break;
        }
      }
    }, 200);
  }
}
