import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import { ToastrService } from 'ngx-toastr';   
import ErrosSql from '../servi√ßos/erroSQL'

@Component({
  selector: 'app-modal-login-cadastro',
  templateUrl: './modal-login-cadastro.component.html',
  styleUrls: ['./modal-login-cadastro.component.css']
})
export class ModalLoginCadastroComponent implements OnInit {

  tipoSenha1 = false;
  tipoSenha2 = false;

  postData(){
    var passwordStatus;
    var password = ()=>{
      if($("#senha_cadastro").val() == $("#confirmSenha_cadastro").val()){
        passwordStatus = true;
        return $("#senha_cadastro").val()
      }else{
        this.toastr.warning('Por favor digite as senhas novamente','Senhas n√£o v√°lidas').onShown.subscribe(()=>{
          setTimeout(() => {
            this.toastr.clear()
          }, 1500);
        })
        passwordStatus = false;
      }
    }

    var cpf = ()=>{
      var numCpf = (<HTMLInputElement>document.getElementById('cpf_cadastro')).value;
      for(var i = 0; i <4;i++){
        numCpf = numCpf.replace(".","");
        numCpf = numCpf.replace("-","");
      }
      return numCpf;
    }

    var gerarToken = ()=>{
      var a = "ABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890".split("");
      var b = [];  
      for (var i=0; i < 40; i++) {
          var j = (Math.random() * (a.length-1)).toFixed(0);
          b[i] = a[j];
      }
      return b.join("");
    }

    $.ajax({
      type: "POST",
      url: "http://127.0.0.2:8080/api/cadastro/cliente",
      data: {
        Nome: $("#nome_cadastro").val(),
        Email: $("#email_cadastro").val(),
        CPF: cpf,
        DT: $("#dataNasc_cadastro").val(),
        Senha: password,
        TOKEN: gerarToken
      }
    });
    location.reload();

  }

  ngOnInit() {
    document.getElementById('eyePasswordCadastro1').addEventListener('click',()=>{
      (<HTMLInputElement>document.getElementById('senha_cadastro')).type = this.tipoSenha1 ? 'password': 'text';     
      this.tipoSenha1 = !this.tipoSenha1;
    })

    document.getElementById('eyePasswordCadastro2').addEventListener('click',()=>{
      (<HTMLInputElement>document.getElementById('confirmSenha_cadastro')).type = this.tipoSenha2 ? 'password': 'text';
      this.tipoSenha2 = !this.tipoSenha2;
    })
  }

  constructor(private toastr: ToastrService) { }

}
