import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery'
import Usuario from '../servi√ßos/getUsuario'

@Component({
  selector: 'app-negociar-home-perfil',
  templateUrl: './negociar-home-perfil.component.html',
  styleUrls: ['./negociar-home-perfil.component.css']
})
export class NegociarHomePerfilComponent implements OnInit {

  usuario = new Usuario();

  conta;
  nome;
  email;
  cpf;
  dt;
  

  constructor() { }

  ngOnInit() {

    setInterval(()=>{
      var input = (<HTMLInputElement>document.getElementById('cpfModalPerfil')).value
      if(input.length > 11){
        $('#cpfModalPerfil').val(input.substr(0,11));
      }
    })

    $('#meuPerfil').click(()=>{
      $('#modalPerfil').click();
      this.usuario.validarTokenUsuario();

      setTimeout(() => {
          this.conta  = this.usuario.conta;
          this.nome = this.usuario.nome;
          this.email = this.usuario.email;
          this.cpf = this.usuario.cpf;
          this.dt = this.usuario.dt;
      }, 300);

    })

    $('#buttonSalvarPefil').click(()=>{
      this.usuario.alterarUsuario();
    })
  }

}
